import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { MaterialModule } from '@angular/material';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { RecomendacionesPage } from '../pages/recomendaciones/recomendaciones';
import { RutinasPage } from '../pages/rutinas/rutinas';
import { EjerciciosPage } from '../pages/ejercicios/ejercicios';
import { AyudaPage } from '../pages/ayuda/ayuda';
import { ContactosPage } from '../pages/contactos/contactos';
import { NutricionPage } from '../pages/nutricion/nutricion';
import { RegistrarsePage } from '../pages/registrarse/registrarse';
import { RetosPage } from '../pages/retos/retos';
import { SedesPage } from '../pages/sedes/sedes';
import { SuplementosPage } from '../pages/suplementos/suplementos';
import { TiendaPage } from '../pages/tienda/tienda';
import { ConfiguracionPage } from '../pages/configuracion/configuracion';



import 'hammerjs';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    RecomendacionesPage,
    RutinasPage,
    EjerciciosPage,
    AyudaPage,
    ContactosPage,
    NutricionPage,
    RegistrarsePage,
    RetosPage,
    SedesPage,
    SuplementosPage,
    TiendaPage,
    ConfiguracionPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/configuracion/configuracion.module#ConfiguracionPageModule', name: 'ConfiguracionPage', segment: 'configuracion', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/recomendaciones/recomendaciones.module#RecomendacionesPageModule', name: 'RecomendacionesPage', segment: 'recomendaciones', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/ejercicios/ejercicios.module#EjerciciosPageModule', name: 'EjerciciosPage', segment: 'ejercicios', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/rutinas/rutinas.module#RutinasPageModule', name: 'RutinasPage', segment: 'rutinas', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/ayuda/ayuda.module#AyudaPageModule', name: 'AyudaPage', segment: 'ayuda', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/contactos/contactos.module#ContactosPageModule', name: 'ContactosPage', segment: 'contactos', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/nutricion/nutricion.module#NutricionPageModule', name: 'NutricionPage', segment: 'nutricion', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/registrarse/registrarse.module#RegistrarsePageModule', name: 'RegistrarsePage', segment: 'registrarse', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/retos/retos.module#RetosPageModule', name: 'RetosPage', segment: 'retos', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/sedes/sedes.module#SedesPageModule', name: 'SedesPage', segment: 'sedes', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/suplementos/suplementos.module#SuplementosPageModule', name: 'SuplementosPage', segment: 'suplementos', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tienda/tienda.module#TiendaPageModule', name: 'TiendaPage', segment: 'tienda', priority: 'low', defaultHistory: [] }
  ]
}),
    MaterialModule,
    [BrowserAnimationsModule]
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    RecomendacionesPage,
    RutinasPage,
    EjerciciosPage,
    AyudaPage,
    ContactosPage,
    NutricionPage,
    RegistrarsePage,
    RetosPage,
    SedesPage,
    SuplementosPage,
    TiendaPage,
    ConfiguracionPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler}
  ]
})
export class AppModule {}



